(function() {
  'use strict';

  angular
    .module('moto', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ngRoute', 'toastr'])

    // Filter for URL
    .filter('encodeURI', encodeURI)
    // Filter for search input
    .filter('startsWithLetter', startsWithLetter)
    // Filter for category
    .filter('property', property)
    // Filter for character limit
    .filter('limitString', limitString);

    function encodeURI() {
        return encodeURI;
    }

    function startsWithLetter() {

      return function (items, letter) {
        var filtered = [];
        var letterMatch = new RegExp(letter, 'i');
        for (var i = 0; i < items.length; i++) {
          var item = items[i];
          if (letterMatch.test(item.title.substring(0, 32))) {
            filtered.push(item);
          }
        }
        return filtered;
      };

    }

    function property($filter) {

      var parseString = function(input){
        return input.split('.');
      };

      function getValue(element, propertyArray) {
        var value = element;

        angular.forEach(propertyArray, function(property) {
          value = value[property];
        });

        return value;
      }

      return function (array, propertyString, target) {
        var properties = parseString(propertyString);

        return $filter('filter')(array, function(item){
          return getValue(item, properties) === target;
        });
      };

    }

    function limitString() {

      return function (value, wordwise, max, tail) {
        if (!value) return '';

        max = parseInt(max, 10);
        if (!max) return value;
        if (value.length <= max) return value;

        value = value.substr(0, max);
        if (wordwise) {
          var lastspace = value.lastIndexOf(' ');
          if (lastspace != -1) {
            value = value.substr(0, lastspace);
          }
        }

        return value + (tail || ' …');
        //  | limitString:true:100:' ...'
      };
    }

})();

(function() {
  'use strict';

  angular
    .module('moto')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

